/*eslint-disable*/
import React, { useState } from "react";
import {
  Card,
  Button,
  Row,
  Col,
  CardBody,
  Form,
  CardFooter,
  FormGroup,
  Label,
  Input,
  Container,
} from "reactstrap";

import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import SimpleHeader from "components/Headers/SimpleHeader.js";

const CreateRak = () => {
  const token = localStorage.token;
  const warehouse = localStorage.warehouse;
  const username = localStorage.username;
  const history = useHistory();

  const [isLoading, setLoading] = useState(false);
  const [keterangan, setKeterangan] = useState("");
  const [minimumstock, setMinimumStock] = useState([]);
  const [area, setArea] = useState([]);
  const [sisi, setSisi] = useState([]);
  const [tingkat, setTingkat] = useState([]);
  const [panjang, setPanjang] = useState([]);
  const [lebar, setLebar] = useState([]);
  const [tinggi, setTinggi] = useState([]);
  const [status, setStatus] = useState([]);
  const [image, setImage] = useState("");

  function CreateData() {
    setLoading(true);{
    CreateRak();
    }
  }
  const CreateRak = () => {
    const headers = {
      "Content-Type": "multipart/form-data",
      Authorization: `Bearer ${token}`,
    };
    
    const myjson = JSON.stringify ({
        warehouse_id: parseInt(warehouse) ,
        area,
        sisi,
        tingkat,
        panjang,
        lebar,
        tinggi,
        minimum_stok: parseInt(minimumstock),   
        active_flag: status,
        keterangan,
        active_flag:1
    });     
    let data = new FormData();
    data.append("body",myjson)
    data.append("desain",image)
    axios
      .post(`${process.env.REACT_APP_API_BASE_URL}/rak/save`, data, { headers })
      .then(function (response) {
        history.push("/admin/rak");
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    {
      CreateData();
    }
  };

  return (
    <div>
      <SimpleHeader name="Tambah Rak" parentName="Master" />
      <Container className="mt--6" fluid>
        <Row>
          <div className="col">
            <Card>
              <Form onSubmit={handleSubmit}>
                <CardBody>
                  <Row md="12">
                      <Col md="6">
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            sm={4}
                          >
                            Area
                          </Label>
                          <Col sm={7}>
                            <Input
                              type="text"
                              name="Area"
                              placeholder="Masukan Area"
                              value={area}
                              onChange={(e) => {
                                setArea(e.target.value);
                              }}
                            />
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            sm={4}
                          >
                            Sisi
                          </Label>
                          <Col sm={7}>
                            <Input
                              type="text"
                              name="SISI"
                              placeholder="Masukan sisi"
                              value={sisi}
                              onChange={(e) => {
                                setSisi(e.target.value);
                              }}
                            />
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            sm={4}
                          >
                            Tingkat
                          </Label>
                          <Col sm={7}>
                            <Input
                              type="text"
                              name="Tinggi"
                              placeholder="Masukan Tingkat"
                              value={tingkat}
                              onChange={(e) => {
                                setTingkat(e.target.value);
                              }}
                            />
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            sm={4}
                          >
                            Dimensi
                          </Label>
                          <Col sm={2}>
                            <Input
                            type="text"
                            name="panjang"
                            placeholder="P"
                            value={panjang}
                           onChange={(e) => {
                              setPanjang(e.target.value);
                            }}
                            />
                          </Col>
                          <Col sm={3}>
                            <Input
                            type="text"
                            name="lebar"
                            placeholder="L"
                            value={lebar}
                            onChange={(e) => {
                              setLebar(e.target.value);
                             
                            }}
                            />
                          </Col>
                          <Col sm={2}>
                            <Input
                            type="text"
                            name="tinggi"
                            placeholder="T"
                            value={tinggi}
                            onChange={(e) => {
                              setTinggi(e.target.value);
                             
                            }}
                            />
                          </Col>
                        </FormGroup>
                      </Col>
                      <Col md="6">
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            sm={4}
                          >
                            Minimum Stock
                          </Label>
                          <Col sm={7}>
                            <Input
                            type="text"
                            name="Minimum Stock"
                            placeholder="Masukan Stock"
                            value={minimumstock}
                            onChange={(e) => {
                              setMinimumStock(e.target.value);
                              }}
                            />
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Status
                              </Label>
                                <Col sm={7}>
                                  <div style={{ display: "flex" }}>
                                    <div className="custom-control custom-radio mb-3">
                                      <Input
                                        className="custom-control-input"
                                        id="customRadio11"
                                        name="custom-radio-4"
                                        type="checkbox"
                                        value={1}
                                        checked={status === 1}
                                        onChange={() => setStatus(1)}
                                      />
                                      <Label
                                        className="custom-control-label"
                                        htmlFor="customRadio11"
                                      >
                                        Aktif
                                      </Label>
                                    </div>
                                    <div
                                      className="custom-control custom-radio mb-3"
                                      style={{ marginLeft: "20px" }}
                                    >
                                      <Input
                                        className="custom-control-input"
                                        id="customRadio12"
                                        name="custom-radio-4"
                                        type="radio"
                                        value={2}
                                        checked={status === 2}
                                        onChange={() => setStatus(2)}
                                      />
                                      <Label
                                        className="custom-control-label"
                                        htmlFor="customRadio12"
                                      >
                                        Tidak Aktif
                                      </Label>
                                    </div>
                                  </div>
                                </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label
                                for="exampleEmail"
                                sm={4}
                            >
                                FOTO 
                            </Label>
                            <Col sm={7}>
                            <Input
                                id="exampleFile"
                                name="file"
                                type="file"
                                onChange={(event) => {
                                    setImage(event.target.files[0]);
                                }}
                                />
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Keterangan
                              </Label>
                              <Col sm={7}>
                                <Input
                                  type="textarea"
                                  name="keterangan"
                                  placeholder="Masukan keterangan"
                                  value={keterangan}
                                  onChange={(e) => {
                                    setKeterangan(e.target.value);
                                  }}
                                />
                              </Col>
                        </FormGroup>
                      </Col>
                  </Row>
                </CardBody>
                <CardFooter>
                    {!isLoading && (<Button color="primary" type="submit">
                      Simpan
                    </Button>)}
                    {isLoading && (
                      <Button color="primary" disabled>
                        <i className="fas fa-spinner fa-spin"></i>
                        {""}
                        loading...
                      </Button>
                    )}
                    <Link className="btn btn-info" to="/admin/item">
                      Kembali
                    </Link>
                </CardFooter>
               </Form>      
            </Card>
          </div>
        </Row>
      </Container>
    </div>
    
  );
};

export default CreateRak;