/*eslint-disable*/
import React, { useEffect, useState } from "react";
import {
  Card,
  CardBody,
  Label,
  FormGroup,
  Row,
  Input,
  Col,
  CardFooter,
  Container,
  Button,
  Form,
} from "reactstrap";
import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import SimpleHeader from "components/Headers/SimpleHeader.js";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import ToolkitProvider from 'react-bootstrap-table2-toolkit';


export default function Edit(props) {
  const username = localStorage.username;
  const token = localStorage.token;
  const warehouse = localStorage.warehouse;
  
  let history = useHistory();
  const [usernamePo, setUsernamePo] = useState("");
  const [isLoading, setLoading] = useState(false);
  const [description, setDescription] = useState("");
  const [person, setPerson] = useState("");
  const [persons, setPersons] = useState([]);
  const [ordercode,setOrderCode]= useState("");
  const [jangkawaktu,setJangkaWaktu] = useState([]);
  const [keteranganpayment,setKeteranganPayment] = useState("");
  const [paymentmethod,setPaymentMethod] = useState("");
  const [keteranganpo,setKeteranganPo] = useState("");
  const [termin,setTermin] = useState([]);
  const [durasitempo,setDurasiTempo] = useState("");
  const [poType, setPoType] = useState("");
  const [approve, setApprove] = useState([]);
  const [rowIndex, setRowIndex] = useState(0);
  const [page, setPage] = useState(1);
  const [perPage, setPerpage] = useState(10);
  const [totalItem, setTotalItem] = useState(0);
  const [currentSort, setCurrentSort] = useState("");


  useEffect(() => {
    getById();
      }, []);

  const getById = () => {
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    axios
      .get(
        `${process.env.REACT_APP_API_BASE_URL}/purchase-order/get/${props.match.params.id}`,
        { headers }
      )
      .then((data) => {
        console.log(data);
        getPo(data.data.response_data.id);
        setOrderCode(data.data.response_data.order_code);
        setUsernamePo(data.data.response_data.username_po);
        setPaymentMethod(data.data.response_data.payment_method);
        setKeteranganPo(data.data.response_data.keterangan_po);
        setJangkaWaktu(data.data.response_data.jangka_waktu);
        setKeteranganPayment(data.data.response_data.keterangan_payment );
        setTermin(data.data.response_data.is_cicil);
        setDurasiTempo(data.data.response_data.durasi_po);
        setPerson(data.data.response_data.person);
        setPoType(data.data.response_data.po_type);
        setApprove(data.data.response_data.approve);

      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const getPo = (poId) => {
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    const data = {
      po_id: parseInt(poId),
    };
    axios
      .post(
        `${process.env.REACT_APP_API_BASE_URL}/purchase-order/po-items`,
        data,
        {
          headers,
        }
      )
      .then((data) => {
        getPerson();

      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const getPerson = () => {
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    axios
      .get(`${process.env.REACT_APP_API_BASE_URL}/person/list`, { headers })
      .then((data) => {
        setPersons(data.data.response);
      })
      .catch(function (error) {
        console.log(error);
      });
  };


  let paginationOption = {
    page: page,
    alwaysShowAllBtns: true,
    override: true,
    showTotal: true,
    withFirstAndLast: false,
    sizePerPage: perPage,
    totalSize: totalItem,
    onPageChange: (page) => {
      updateDataTable(page, perPage, currentSort, uomCode, description);
    },
    sizePerPageRenderer: () => (
      <div className="dataTables_length" id="datatable-basic_length">
        <label>
          Show{" "}
          {
            <select
              name="datatable-basic_length"
              aria-controls="datatable-basic"
              className="form-control form-control-sm"
              onChange={(e) => {
                updateDataTable(page, e.target.value, currentSort)
              }}
            >
              <option value="10">10</option>
              <option value="20">20</option>
              <option value="25">25</option>
              <option value="50">50</option>
            </select>
          }{" "}
          entries.
        </label>
      </div>
    ),
  }

  const handleTableChange = (type, { sortField, sortOrder }) => {
    if (type === "sort") {
      let sort = `${sortField} ${sortOrder}`
      updateDataTable(page, perPage, sort,  uomCode, description)
    }
  }
  function EditData() {
    setLoading(true);
    let data = {
    warehouse_id : parseInt(warehouse),
    username_po : usernamePo,
    payment_method : paymentmethod,
    jangka_waktu : jangkawaktu,
    keterangan_payment : keteranganpayment,
    po_type: poType,
    durasi_po: durasitempo,
    person: person,
    is_cicil: parseInt(termin),
    approve: parseInt(approve),
    keterangan_po: keteranganpo,
    username_admin: "",
    admin_approval: 0,
    keterangan_admin: "",
    username_validator : "",
    clear : 0,
    keterangan_validator : "",
    username_gudang : "",
    status_barang : 0,
    keterangan_gudang : "",
    username_kurir : "",
    person:1,
    active_flag : 1,
    po_type : 1,
    items : [],
    };
    axios
      .post(
        `${process.env.REACT_APP_API_BASE_URL}/purchase-order/update/${props.match.params.id}`,
        data,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then(function (response) {
        history.push("/admin/po");
        console.log(data);
      })
      .then((json) => {
        setLoading(false);
      })
      .catch(function (error) {
        console.log(error);
      });
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    {
      EditData();
    }
  };

  return (
    <div> 
      <SimpleHeader name="Validasi Penawaran PO" parentName="PO" />
      <Container className="mt--6" fluid>
        <Row>
          <div className="col">
            <Card>
              <CardBody>
                
                <Card>
                <Form onSubmit={handleSubmit}> 
                  <CardBody>
                    <Row md="12">
                        <Col md="6">
                          
                            <Col sm={7}>
                              <Input
                              disabled
                                type="hidden"
                                name="desc"
                                value={ordercode}
                                onChange={(e) => {
                                  setDescription(e.target.value);
                                }}
                              />
                            </Col>
                            <Col sm={7}>
                              <Input
                                type="hidden"
                                name="desc"
                                placeholder="Masukan Keterangan PO"
                                value={usernamePo}
                                onChange={(e) => {
                                  setUsernamePo(e.target.value);
                                }}
                              />
                            </Col>
                          <FormGroup row>
                            <Label
                              for="exampleEmail"
                              sm={4}
                            >
                                Nama Supplier
                            </Label>
                            <Col sm={7}>
                            <Input
                            disabled
                                name="kategory"
                                type="select"
                                value={person}
                                onChange={(e) => {
                                  setPerson(e.target.value);
                                }}
                              >
                                <option value=''>Pilih Supplier</option>
                                {persons.map((person, key) => {
                                    return (
                                      <option key={key} value={person.id}>
                                        {person.person_name}
                                      </option>
                                    );
                                  })}
                              </Input>
                            </Col>
                          </FormGroup>
                          <FormGroup row>
                            <Label
                              for="exampleEmail"
                              sm={4}
                            >
                              keterangan PO
                            </Label>
                            <Col sm={7}>
                              <Input
                              disabled
                                type="text"
                                name="desc"
                                placeholder="Masukan Keterangan PO"
                                value={keteranganpo}
                                onChange={(e) => {
                                  setKeteranganPo(e.target.value);
                                }}
                              />
                            </Col>
                          </FormGroup>
                          <FormGroup row>
                            <Label
                              for="exampleEmail"
                              sm={4}
                            >
                              keterangan Payment
                            </Label>
                            <Col sm={7}>
                              <Input
                              disabled
                                type="text"
                                name="desc"
                                placeholder="Masukan Keterangan PO"
                                value={keteranganpayment}
                                onChange={(e) => {
                                  setDescription(e.target.value);
                                 
                                }}
                              />
                            </Col>
                          </FormGroup>
                          <FormGroup row>
                            <Label
                              for="exampleEmail"
                              sm={4}
                            >
                              Tipe PO
                            </Label>
                            <Col sm={7}>
                            <Input
                            disabled
                                name="paymentMethod"
                                type="select"
                                value={poType}
                                onChange={(e) => {
                                  setPoType(e.target.value);
                                }}
                              >
                                <option value="">Pilih Payment Method</option>
                                <option value={1}>Konvensional</option>
                                <option value={2}>Indent</option>
                              </Input>
                            </Col>
                          </FormGroup>
                        </Col>
                        <Col md="6">
                          <FormGroup row>
                            <Label
                              for="exampleEmail"
                              sm={4}
                            >
                              Durasi Tempo
                            </Label>
                            <Col sm={7}>
                            <Input
                            disabled
                                type="text"
                                name="barcode"
                                placeholder="Masukan Durasi Tempo"
                                value={durasitempo}
                                onChange={(e) => {
                                  setDurasiTempo(e.target.value);
                                  
                                }}
                              />
                            </Col>
                          </FormGroup>
                          <FormGroup row>
                            <Label
                              for="exampleEmail"
                              sm={4}
                            >
                              Jangka Waktu
                            </Label>
                            <Col sm={7}>
                            <Input
                            disabled
                                type="number"
                                name="jangkawaktu"
                                placeholder="Masukan Jangka Waktu"
                                value={jangkawaktu}
                                onChange={(e) => {
                                  setJangkaWaktu(e.target.value);
                                }}
                              />
                            </Col>
                          </FormGroup>
                          <FormGroup row>
                            <Label
                              for="exampleEmail"
                              sm={4}
                            >
                              Termin
                            </Label>
                            <Col sm={7}>
                            <Input
                            disabled
                            name="satuan"
                            type="select"
                            value={termin}
                            onChange={(e) => {
                              setTermin(e.target.value);
                            }}
                          >
                            <option value={0}>Pilih Cicilan</option>
                            <option value={1}>Lunas</option>
                            <option value={2}>Belum Lunas</option>
                            
                              </Input>
                            </Col>
                          </FormGroup>
                          <FormGroup row>
                            <Label
                              for="exampleEmail"
                              sm={4}
                            >
                              Metode Pembayaran
                            </Label>
                            <Col sm={7}>
                            <Input
                          disabled
                          name="paymentMethod"
                          type="select"
                          value={paymentmethod}
                          onChange={(e) => {
                            setPaymentMethod(e.target.value);
                          }}
                        >
                          <option value="">Pilih Payment Method</option>
                          <option value={1}>Tunai</option>
                          <option value={2}>Tempo/Termin/Utang</option>
                        </Input>
                            </Col>
                          </FormGroup>
                          <FormGroup row>
                            <Label
                              for="exampleEmail"
                              sm={4}
                            >
                              Approve
                            </Label>
                              <Col sm={6}>
                                <div style={{ display: "flex" }}>
                                  <div className="custom-control custom-radio mb-3">
                                    <Input
                                      className="custom-control-input"
                                      id="customRadio8"
                                      name="custom-radio-3"
                                      type="radio"
                                      value={1}
                                      checked={approve === 1}
                                      onChange={() => setApprove(1)}
                                    />
                                    <Label
                                      className="custom-control-label"
                                      htmlFor="customRadio8"
                                    >
                                      Disetujui
                                    </Label>
                                  </div>
                                  <div
                                    className="custom-control custom-radio mb-3"
                                    style={{ marginLeft: "20px" }}
                                  >
                                    <Input
                                      className="custom-control-input"
                                      id="customRadio9"
                                      name="custom-radio-3"
                                      type="radio"
                                      value={2}
                                      checked={approve === 2}
                                      onChange={() => setApprove(2)}
                                    />
                                    <Label
                                      className="custom-control-label"
                                      htmlFor="customRadio9"
                                    >
                                      Ditolak
                                    </Label>
                                  </div>
                                  <div
                                    className="custom-control custom-radio mb-3"
                                    style={{ marginLeft: "20px" }}
                                  >
                                    <Input
                                      className="custom-control-input"
                                      id="customRadio10"
                                      name="custom-radio-3"
                                      type="radio"
                                      value={3}
                                      checked={approve === 3}
                                      onChange={() => setApprove(3)}
                                    />
                                    <Label
                                      className="custom-control-label"
                                      htmlFor="customRadio10"
                                    >
                                      Dibatalkan
                                    </Label>
                                  </div>
                                </div>
                              </Col>
                            </FormGroup>
                        </Col>
                    </Row>
                    <ToolkitProvider
                            rowNumber={rowIndex}
                            data={""}
                            keyField="id"
                            columns={[
                              {
                                dataField: "no",
                                text: "No",
                                sort: true,
                                page: 1,
                                formatter: (cell, row, index) => {
                                  let currentRow = ++index;
                                  return currentRow + rowIndex;
                                }
                              },
                              {
                                dataField: "kode_po",
                                text: "Kode PO",
                                sort: true,
                              },
                           
                                {
                                  dataField: "description",
                                  text: "Deskripsi SO",
                                  sort: true,
                                },
                            ]}
                          >
                            {(props) => (
                              <div className="py-4 table-responsive">
                                <BootstrapTable
                                  remote
                                  {...props.baseProps}
                                  bootstrap4={true}
                                  bordered={false}
                                  hover={true}
                                  pagination={paginationFactory({ ...paginationOption })}
                                  onTableChange={handleTableChange}
                                />
                              </div>
                            )}
                    </ToolkitProvider>
                  </CardBody>
                  <CardFooter>
                  <Row md="12">
                    <Col md="9">
                    </Col>
                    <Col md="3">
                    {!isLoading && (
                      <Button color="primary" type="submit">
                        Simpan
                      </Button>
                    )}
                    {isLoading && (
                      <Button color="primary" disabled>
                        <i className="fas fa-spinner fa-spin"></i>
                        {""}
                        loading...
                      </Button>
                    )}
                    <Link className="btn btn-info" to="/admin/po">
                      Kembali
                    </Link>
                    </Col>
                  </Row>
                  </CardFooter>
                  </Form>
                  </Card>
              </CardBody>
              </Card>
          </div>
        </Row>
        </Container>
    </div>
  );
}
