/*eslint-disable*/
import React, { useEffect, useState } from "react";
import {
  Card,
  Button,
  Row,
  Col,
  CardBody,
  Form,
  CardFooter,
  FormGroup,
  Label,
  Input,
  Container,
} from "reactstrap";

import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import SimpleHeader from "components/Headers/SimpleHeader.js";

const CreateWarehouse = () => {
  const token = localStorage.token;
  const history = useHistory();

  const [isLoading, setLoading] = useState(false);
  const [name, setName] = useState("");
  const [address, setAddress] = useState("");
  const [city,setCity] = useState([]);
  const [longitude, setLongitude] = useState([]);
  const [latitude, setLatitude] = useState([]);
  const [province, setProvince] = useState([]);
  const [phone, setPhone] = useState("");
  const [fax, setFax] = useState("");
  const [status,setStatus] = useState([]); 
  const [npwp, setNpwp] = useState("");
  const [image, setImage] = useState("");
  const [provinces, setProvinces] = useState([]);
  const [warehousepusat, setWarehousePusat] = useState([]);
  const [warehousepusats, setWarehousePusats] = useState([]);
  const [citys, setCitys] = useState([]);

  useEffect(() => {
    getProvinsi();
    getPusat();
  }, []);

  const getProvinsi = () => {
    const headers = {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`
      }
    axios.get(`${process.env.REACT_APP_API_BASE_URL}/province/list`, { headers
    })
    .then(data => {
      setProvinces(data.data.response_data);
    })
      .catch(function (error) {
        console.log(error)
      })
  }

  const getKota = (id) => {
    const headers = {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`
      }
    axios.get(`${process.env.REACT_APP_API_BASE_URL}/city?province_id=${id}`, { headers
    })
    .then(data => {
      setCitys(data.data.response_data);
    })
      .catch(function (error) {
        console.log(error)
      })
  }

  const getPusat = () => {
    const headers = {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`
      }
    axios.get(`${process.env.REACT_APP_API_BASE_URL}/warehouse/pusat/list`, { headers
    })
    .then(data => {
      setWarehousePusats(data.data.response);
    })
      .catch(function (error) {
        console.log(error)
      })
  }
  
  function CreateData() {
    setLoading(true);{
    CreateRak();
    }
  }

  const CreateRak = () => {
    const headers = {
      "Content-Type": "multipart/form-data",
      Authorization: `Bearer ${token}`,
    };
    
    const myjson = JSON.stringify ({
        
        name,
        is_indent:parseInt(status),
        active_flag:1,
        pusat_id:parseInt(warehousepusat),
        address,
        level:2,
        city : parseInt(city),
        longitude,
        latitude,
        province:parseInt(province),
        phone,
        fax,
        npwp

    });     
    let data = new FormData();
    data.append("body",myjson)
    data.append("logo",image)
    axios
      .post(`${process.env.REACT_APP_API_BASE_URL}/warehouse/toko/save`, data, { headers })
      .then(function (response) {
        history.push("/admin/warehouse");
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    {
      CreateData();
    }
  };

  return (
    <div>
      <SimpleHeader name="Warehouse Toko" parentName="Master" />
      <Container className="mt--6" fluid>
        <Row>
          <div className="col">
            <Card>
              <Form onSubmit={handleSubmit}>
                <CardBody>
                  <Row md="12">
                      <Col md="6">
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            sm={4}
                          >
                            Nama
                            <span className="text-danger">*</span>
                          </Label>
                          <Col sm={7}>
                            <Input
                              type="text"
                              name="Nama"
                              placeholder="Masukan Nama"
                              value={name}
                              onChange={(e) => {
                                setName(e.target.value);
                              }}
                            />
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            sm={4}
                          >
                            Alamat
                            <span className="text-danger">*</span>
                          </Label>
                          <Col sm={7}>
                            <Input
                              type="text"
                              name="Alamat"
                              placeholder="Masukan Alamat"
                              value={address}
                              onChange={(e) => {
                                setAddress(e.target.value);
                              }}
                            />
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label
                                for="exampleEmail"
                                sm={4}
                            >
                                Pusat
                                <span className="text-danger">*</span>
                            </Label>
                            <Col sm={7}>
                                <Input
                                    type="select"
                                    value={warehousepusat}
                                    onChange={(e) => {
                                    setWarehousePusat(e.target.value);
                                    
                                    }}
                                >
                                    <option>Pilih Warehouse Pusat</option>
                                    {
                                    warehousepusats.map((a, key) => {
                                        return <option key={key} value={a.id}>{a.name}</option>
                                    })
                                    }
                                </Input>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label
                                for="exampleEmail"
                                sm={4}
                            >
                                Provinsi
                                <span className="text-danger">*</span>
                            </Label>
                            <Col sm={7}>
                                <Input
                                    type="select"
                                    value={province}
                                    onChange={(e) => {
                                    setProvince(e.target.value);
                                    getKota(e.target.value);
                                    }}
                                >
                                    <option>Pilih Provinsi</option>
                                    {
                                    provinces.map((a, key) => {
                                        return <option key={key} value={a.id}>{a.name}</option>
                                    })
                                    }
                                </Input>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label
                                for="exampleEmail"
                                sm={4}
                            >
                                Kota
                                <span className="text-danger">*</span>
                            </Label>
                            <Col sm={7}>
                                <Input
                                    name="function"
                                    type="select"
                                    value={city}
                                    onChange={(e) => {
                                    setCity(e.target.value);
                                    }}
                                >
                                    <option>Pilih Kota</option>
                                    {
                                    citys.map((b, key) => {
                                        return <option key={key} value={b.id}>{b.name}</option>
                                    })
                                    }
                                </Input>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            sm={4}
                          >
                            Longitude
                            <span className="text-danger">*</span>
                          </Label>
                          <Col sm={7}>
                            <Input
                              type="text"
                              name="Longitude"
                              placeholder="Masukan Longitude"
                              value={longitude}
                              onChange={(e) => {
                                setLongitude(e.target.value);
                              }}
                            />
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            sm={4}
                          >
                            Latitude
                            <span className="text-danger">*</span>
                          </Label>
                          <Col sm={7}>
                            <Input
                              type="text"
                              name="Latitude"
                              placeholder="Masukan Latitude"
                              value={latitude}
                              onChange={(e) => {
                                setLatitude(e.target.value);
                              }}
                            />
                          </Col>
                        </FormGroup>
                      </Col>
                      <Col md="6">
                      <FormGroup row>
                          <Label
                            for="exampleEmail"
                            sm={4}
                          >
                            Phone
                            <span className="text-danger">*</span>
                          </Label>
                          <Col sm={7}>
                            <Input
                              type="text"
                              name="Phone"
                              placeholder="Masukan Phone"
                              value={phone}
                              onChange={(e) => {
                                setPhone(e.target.value);
                              }}
                            />
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            sm={4}
                          >
                            Fax
                            <span className="text-danger">*</span>
                          </Label>
                          <Col sm={7}>
                            <Input
                              type="text"
                              name="Fax"
                              placeholder="Masukan Fax"
                              value={fax}
                              onChange={(e) => {
                                setFax(e.target.value);
                              }}
                            />
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            sm={4}
                          >
                            Npwp
                            <span className="text-danger">*</span>
                          </Label>
                          <Col sm={7}>
                            <Input
                              type="text"
                              name="Npwp"
                              placeholder="Masukan Npwp"
                              value={npwp}
                              onChange={(e) => {
                                setNpwp(e.target.value);
                              }}
                            />
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Status
                                <span className="text-danger">*</span>
                              </Label>
                                <Col sm={7}>
                                  <div style={{ display: "flex" }}>
                                    <div className="custom-control custom-radio mb-3">
                                      <Input
                                        className="custom-control-input"
                                        id="customRadio11"
                                        name="custom-radio-4"
                                        type="checkbox"
                                        value={1}
                                        checked={status === 1}
                                        onChange={() => setStatus(1)}
                                      />
                                      <Label
                                        className="custom-control-label"
                                        htmlFor="customRadio11"
                                      >
                                        STOK GUDANG
                                      </Label>
                                    </div>
                                    <div
                                      className="custom-control custom-radio mb-3"
                                      style={{ marginLeft: "20px" }}
                                    >
                                      <Input
                                        className="custom-control-input"
                                        id="customRadio12"
                                        name="custom-radio-4"
                                        type="radio"
                                        value={2}
                                        checked={status === 2}
                                        onChange={() => setStatus(2)}
                                      />
                                      <Label
                                        className="custom-control-label"
                                        htmlFor="customRadio12"
                                      >
                                        STOK INDENT
                                      </Label>
                                    </div>
                                  </div>
                                </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label
                                for="exampleEmail"
                                sm={4}
                            >
                                FOTO 
                                <span className="text-danger">*</span>
                            </Label>
                            <Col sm={7}>
                            <Input
                                id="exampleFile"
                                name="file"
                                type="file"
                                onChange={(event) => {
                                    setImage(event.target.files[0]);
                                }}
                                />
                            </Col>
                        </FormGroup>
                      </Col>
                  </Row>
                </CardBody>
                <CardFooter>
                    {!isLoading && (<Button color="primary" type="submit">
                      Simpan
                    </Button>)}
                    {isLoading && (
                      <Button color="primary" disabled>
                        <i className="fas fa-spinner fa-spin"></i>
                        {""}
                        loading...
                      </Button>
                    )}
                    <Link className="btn btn-info" to="/admin/warehouse">
                      Kembali
                    </Link>
                </CardFooter>
               </Form>      
            </Card>
          </div>
        </Row>
      </Container>
    </div>
    
  );
};

export default CreateWarehouse;