/*eslint-disable*/
import React, { useEffect, useState } from "react";
import { 
  Card,
  CardImg, 
  Row, 
  CardBody, 
  Col,
  Label,
  CardHeader, 
  FormGroup,
  Input,
  Container,
  ButtonGroup,
  Button,
  Modal, 
  ModalHeader, 
  ModalBody, 
  ModalFooter
} from "reactstrap";
import axios from "axios";
import SimpleHeader from "components/Headers/SimpleHeader.js";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";

const DetailItems = (props) => {
  const token = localStorage.token;
  const [alert, setAlert] = React.useState(null);
  const [hide, setHide] = useState(true);
  const [rowIndex, setRowIndex] = useState(0);

  const [modal, setModal] = useState(false);
  const toggle = () => setModal(!modal);

  const [isOpen, setIsOpen] = useState(false);
  const toggleOpen = () => setIsOpen(!isOpen);
  const [page, setPage] = useState(1);
  const [perPage, setPerpage] = useState(10);
  const [totalItem, setTotalItem] = useState(0);
  const [currentSort, setCurrentSort] = useState("");

  const [area ,setArea] = useState([]);
  const [sisi, setSisi] = useState([]);
  const [tingkat, setTingkat] = useState([]);
  const [itemLocation, setItemLocation] = useState([]);
  const [logPrice, setLogPrice] = useState([]);
  const [itemParts, setItemParts] = useState([]);

  let paginationOption = {
    page: page,
    alwaysShowAllBtns: true,
    override: true,
    showTotal: true,
    withFirstAndLast: false,
    sizePerPage: perPage,
    totalSize: totalItem,
    onPageChange: (page) => {
      updateDataTable(page, perPage, currentSort);
    },
    sizePerPageRenderer: () => (
      <div className="dataTables_length" id="datatable-basic_length">
        <label>
          Show{" "}
          {
            <select
              name="datatable-basic_length"
              aria-controls="datatable-basic"
              className="form-control form-control-sm"
              onChange={(e) => {
                updateDataTable(page, e.target.value, currentSort);
              }}
            >
              <option value="10">10</option>
              <option value="20">20</option>
              <option value="25">25</option>
              <option value="50">50</option>
            </select>
          }{" "}
          entries.
        </label>
      </div>
    ),
  };

  const updateDataTable = (page, perPage, sort) => {
    setPage(page);
    setPerpage(perPage);
    setRowIndex((page - 1) * perPage);
    setCurrentSort(sort);
  };

  const handleTableChange = (type, { sortField, sortOrder }) => {
    if (type === "sort") {
      let sort = `${sortField} ${sortOrder}`;
      updateDataTable(page, perPage, sort);
    }
  };

  useEffect(() => {
    getById();
  }, []);

  const getById = () => {
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    axios
      .get(
        `${process.env.REACT_APP_API_BASE_URL}/items/${props.match.params.id}`,
        { headers }
      )
      .then((data) => {
        setItemLocation(data.data.response.items_location);
        setLogPrice(data.data.response.log_price);
        setItemParts(data.data.response.items_part);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const formatRupiah = (money) => {
    return new Intl.NumberFormat('id-ID',
      { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }
    ).format(money);
  }


  return (
    <div>
      {alert}
      <SimpleHeader name="Item Details" parentName="Admin" />
      <Container className="mt--6" fluid>
        <Row>
          <div className="col">
            <Card>
              <CardHeader>
                <div
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <h3>Item Lokasi</h3>
                </div>
              </CardHeader>
              <CardBody>
                <ToolkitProvider
                  rowNumber={rowIndex}
                  data={itemLocation}
                  keyField="id"
                  columns={[
                    {
                      dataField: "no",
                      text: "#",
                      sort: true,
                      page: 1,
                      formatter: (cell, row, index) => {
                        let currentRow = ++index;
                        return currentRow + rowIndex;
                      },
                    },
                    {
                      dataField: "code_rak",
                      text: "Kode Rak",
                      sort: true,
                    },
                    {
                      dataField: "qty_awal",
                      text: "Quantity Awal",
                      sort: true,
                    },
                    {
                      dataField: "qty_masuk",
                      text: "Quantity Masuk",
                      sort: true,
                    },
                    {
                      dataField: "qty_keluar",
                      text: "Quantity Keluar",
                      sort: true,
                    },
                    {
                      dataField: "",
                      text: "",
                      formatter: (cell, row, index) => {
                        return (
                          <ButtonGroup>
                            <Button color="danger" onClick={toggle}>
                              DETAIL
                            </Button>
                          </ButtonGroup>
                        );
                      },
                    },
                  ]}
                >
                  {(props) => (
                    <div className="py-4 table-responsive">
                      <BootstrapTable
                        remote
                        {...props.baseProps}
                        bootstrap4={true}
                        bordered={false}
                        hover={true}
                        pagination={paginationFactory({ ...paginationOption })}
                        onTableChange={handleTableChange}
                      />
                    </div>
                  )}
                </ToolkitProvider>
              </CardBody>
            </Card>
            <Card>
              <CardHeader>
                <div
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <h3>Item Part</h3>
                </div>
              </CardHeader>
              <CardBody>
                <ToolkitProvider
                  rowNumber={rowIndex}
                  data={itemParts}
                  keyField="id"
                  columns={[
                    {
                      dataField: "no",
                      text: "#",
                      sort: true,
                      page: 1,
                      formatter: (cell, row, index) => {
                        let currentRow = ++index;
                        return currentRow + rowIndex;
                      },
                    },
                    {
                      dataField: "item_code",
                      text: "Kode Item",
                      sort: true,
                    },
                    {
                      dataField: "item_name",
                      text: "Nama Item",
                      sort: true,
                    },
                    {
                      dataField: "harga",
                      text: "Harga",
                      sort: true,
                      formatter: (cell, row) => {
                        return formatRupiah(row.harga)
                      }
                    },
                    {
                      dataField: "qty",
                      text: "Quantity",
                      sort: true,
                    },
                    {
                      dataField: "sub_total",
                      text: "Sub Total",
                      sort: true,
                      formatter: (cell, row) => {
                        return formatRupiah(row.sub_total)
                      }
                    },
                  ]}
                >
                  {(props) => (
                    <div className="py-4 table-responsive">
                      <BootstrapTable
                        remote
                        {...props.baseProps}
                        bootstrap4={true}
                        bordered={false}
                        hover={true}
                        pagination={paginationFactory({ ...paginationOption })}
                        onTableChange={handleTableChange}
                      />
                    </div>
                  )}
                </ToolkitProvider>
              </CardBody>
            </Card>
            <Card>
              <CardHeader>
                <div
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <h3>Log Harga</h3>
                </div>
              </CardHeader>
              <CardBody>
                <ToolkitProvider
                  rowNumber={rowIndex}
                  data={logPrice}
                  keyField="id"
                  columns={[
                    {
                      dataField: "no",
                      text: "#",
                      sort: true,
                      page: 1,
                      formatter: (cell, row, index) => {
                        let currentRow = ++index;
                        return currentRow + rowIndex;
                      },
                    },
                    {
                      dataField: "username",
                      text: "Username",
                      sort: true,
                    },
                    {
                      dataField: "nominal_1",
                      text: "Nominal 1",
                      sort: true,
                      formatter: (cell, row) => {
                        return formatRupiah(row.nominal_1)
                      }
                    },
                    {
                      dataField: "nominal_2",
                      text: "Nominal 2",
                      sort: true,
                      formatter: (cell, row) => {
                        return formatRupiah(row.nominal_2)
                      }
                    },
                    {
                      dataField: "min_qty_1",
                      text: "Minimal Quantity 1",
                      sort: true,
                    },
                    {
                      dataField: "min_qty_2",
                      text: "Minimal Quantity 2",
                      sort: true,
                    },
                    {
                      dataField: "harga_beli",
                      text: "Harga Beli",
                      sort: true,
                      formatter: (cell, row) => {
                        return formatRupiah(row.harga_beli)
                      }
                    },
                  ]}
                >
                  {(props) => (
                    <div className="py-4 table-responsive">
                      <BootstrapTable
                        remote
                        {...props.baseProps}
                        bootstrap4={true}
                        bordered={false}
                        hover={true}
                        pagination={paginationFactory({ ...paginationOption })}
                        onTableChange={handleTableChange}
                      />
                    </div>
                  )}
                </ToolkitProvider>
              </CardBody>
            </Card>
          </div>
        </Row>
      </Container>
      {/* modal */}
      <Modal size="xl" isOpen={modal} toggle={toggle} {...props}>
        <ModalHeader toggle={toggle}>RAK LOCATION</ModalHeader>
        <ModalBody >
          <Card >
            <CardBody>
              <CardImg
                alt="Card image Planogram"
                src="https://picsum.photos/900/270?grayscale"
                style={{
                  height: 400
                }}
                width="200%"
              />
              <hr />
              <FormGroup row>
                <Label
                  for="exampleEmail"
                  sm={2}
                >
                  Area
                </Label>
                <Col sm={7}>
                  <Input
                    type="text"
                    name="Area"
                    value={area}
                    onChange={(e) => {
                    setArea(e.target.value);
                    }}
                  />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Label
                  for="exampleEmail"
                  sm={2}
                >
                  Sisi
                </Label>
                <Col sm={7}>
                  <Input
                    type="text"
                    name="Sisi"
                    value={sisi}
                    onChange={(e) => {
                      setSisi(e.target.value);
                    }}
                  />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Label
                  for="exampleEmail"
                  sm={2}
                >
                  Tingkat
                </Label>
                <Col sm={7}>
                  <Input
                    type="text"
                    name="tingkat"
                    value={tingkat}
                    onChange={(e) => {
                      setTingkat(e.target.value);
                    }}
                  />
                </Col>
              </FormGroup>
            </CardBody>
          </Card>
        </ModalBody>
        <ModalFooter>
          <Button color="secondary" onClick={toggle}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  );
};

export default DetailItems;
