/*eslint-disable*/
import React, { useEffect, useState } from "react";
import {
  
  Card,
  Button,
  Row,
  Col,
  CardBody,
  CardHeader,
  CardFooter,
  Table,
  ButtonGroup,
  Form,
  FormGroup,
  UncontrolledTooltip,
  Label,
  Input,
  
} from "reactstrap";
import { Link } from "react-router-dom";
import axios from "axios";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";

const Index = () => {
  const token = localStorage.token;
  const username = localStorage.username;
  const [rowIndex, setRowIndex] = useState(0);
  const redirectPrefix = `/admin/item/edit/`;
  const [isOpen, setIsOpen] = useState(false);
  const toggleOpen = () => setIsOpen(!isOpen);
  const [page, setPage] = useState(1);
  const [perPage, setPerpage] = useState(10);
  const [totalItem, setTotalItem] = useState(0);
  const [currentSort, setCurrentSort] = useState("");
  const [itemType, setItemType] = useState(1);
  const [warehouse, setWaresouse] = useState("");
  const [nameItem, setNameItem] = useState("");
  const [soType,setSoType] = useState("");
  const [kodeso,setKodeSo] = useState("");
  const [keteranganso,setKeteranganSo] = useState("");
  const [soStatus,setSoStatus] = useState("");
  const [payment_method,setPaymentMethod] = useState("");
  const [totaldiskon,setTotalDiskon] = useState("");
  const [ppn,setPpn] = useState("");
  const [promo,setPromo] = useState("");
  const [jangkawaktu,setJangkaWaktu] = useState("");
  const [cicil,setCicil] = useState("");
  const [pengiriman,setPengiriman] = useState("");
  const [barcode, setBarcode] = useState("");
  const [allSo, setAllSo] = useState([]);
  const [itemname,setItemName] = useState('')
  const [idItem, setIdItem] = useState(null)
  const [usernameSo, setUsernameSo] = useState(username);
  const [isLoading, setLoading] = useState(false);
  const [person, setPerson] = useState("");
  const [kodeitem,setKodeItem] = useState("");
  const [qty,setQty] = useState("");
  const [qtyfisik,setQtyFisik] = useState("");
  const [keteranganfisik,setKeteranganFisik] = useState("");
  const [persons, setPersons] = useState([]);
  const [inputList, setInputList] = useState([
    {
      item_id: "",
      qty: "",
      satuan: "",
      harga: "",
      qty_fisik: "",
      keterangan_fisik: "",
    },
  ]);

  function CreateData() {
    setLoading(true);
    
    // updateItem()
    updatePrice(idItem)
  }

  let paginationOption = {
    page: page,
    alwaysShowAllBtns: true,
    override: true,
    showTotal: true,
    withFirstAndLast: false,
    sizePerPage: perPage,
    totalSize: totalItem,
    onPageChange: (page) => {
      updateDataTable(page, perPage, currentSort, nameItem);
    },
    sizePerPageRenderer: () => (
      <div className="dataTables_length" id="datatable-basic_length">
        <label>
          Show{" "}
          {
            <select
              name="datatable-basic_length"
              aria-controls="datatable-basic"
              className="form-control form-control-sm"
              onChange={(e) => {
                updateDataTable(page, e.target.value, currentSort);
              }}
            >
              <option value="10">10</option>
              <option value="20">20</option>
              <option value="25">25</option>
              <option value="50">50</option>
            </select>
          }{" "}
          entries.
        </label>
      </div>
    ),
  };

  const updateDataTable = (page, perPage, sort, nameItem) => {
    getItem(page, perPage, sort, nameItem);
    setPage(page);
    setPerpage(perPage);
    setRowIndex((page - 1) * perPage);
    setCurrentSort(sort);
    setNameItem(nameItem);
    getPrice(page, perPage, sort);
  };

  const handleTableChange = (type, { sortField, sortOrder }) => {
    if (type === "sort") {
      let sort = `${sortField} ${sortOrder}`;
      updateDataTable(page, perPage, sort, nameItem);
    }
  };


  useEffect(() => {
    getPerson();

  }, []);


  const getPerson = () => {
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    axios
      .get(
        `${process.env.REACT_APP_API_BASE_URL}/person/list`,
        { headers }
      )
      .then((data) => {
        setPersons(data.data.response);
      })
      .catch(function (error) {
        console.log(error);
      });
  };




  useEffect(() => {
    getSo(page, perPage, currentSort);
  }, []);

  // fungsi dari ambil data
  const getSo = (page, perPage, currentSort) => {
    let filter = { page: page, per_page: perPage };
    const data = filter;
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    axios
      .post(`${process.env.REACT_APP_API_BASE_URL}/sales-order/page`, data, {
        headers,
      })
      .then((data) => {
        setAllSo(data.data.response_data);
        setPage(data.data.current_page + 1);
        setPerpage(data.data.per_page);
        setTotalItem(data.data.total_item);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const searchItemByCode2 = () => {

    const data = {
      page: 1,
      per_page: 10,
      item_name: itemname,
      item_code: "",
      type:1
    }

    fetch(`${process.env.REACT_APP_API_BASE_URL}/items`, {
      headers: {
        "Content-Type": "application/json",
        'Authorization': `Bearer ${token}`,
      },
      body: JSON.stringify(data),
      method: 'POST'
    })
    .then(j => j.json())
    .then(res => {
      if(res.message === 'Success'){
        
        // call function getPriceById
        const item = res.response[0]
        
        getPriceById(item.id)

        setIdItem(item.id)
        setNameItem(item.item_name)
        setItemCode(item.item_code)
        setCategory(item.kategori_name)
        setNameFunction(item.function_name)
        setMerek(item.merek_name)
        setSatuan(item.uom_code)

      }
    })
  }

  const getPriceById = id => {

    fetch(`${process.env.REACT_APP_API_BASE_URL}/price-detail/${id}`, {
      headers: {
        "Content-Type": "application/json",
        'Authorization': `Bearer ${token}`,
      },
      method: 'GET'
    })
    .then(j => j.json())
    .then(res => {
      if(res.message === 'Get Data Success'){
        const price = res.response_data
        
        setPajakBeli(price.pajak_beli)
        setHargaBeli(price.harga_beli)
        setDiskonNominal(price.diskon_nominal)
        setDiskonPersen(price.diskon_persen)
        setDiskonSuplierNominal(price.diskon_nominal_suplier)
        setDiskonSuplierPersen(price.diskon_persen_suplier)
        setPajakJual(price.pajak_jual)      
        setMargin1(price.persentase_1)
        setMargin2(price.persentase_2)
        setMargin3(price.persentase_3)
        setMargin4(price.persentase_4)
        setMargin5(price.persentase_5)
        setQty1(price.min_qty_1)
        setQty2(price.min_qty_2)
        setQty3(price.min_qty_3)
        setQty4(price.min_qty_4)
        setQty5(price.min_qty_5)
        setPrice1(price.price_1)
        setPrice2(price.price_2)
        setPrice3(price.price_3)
        setPrice4(price.price_4)
        setPrice5(price.price_5)
        
      }
    })
  }

  
  // handle click event of the Remove button
  const handleRemoveClick = (index) => {
    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
  };

  // handle click event of the Add button
  const handleAddClick = () => {
    setInputList([
      ...inputList,
      {
        item_id: "",
        qty: "",
        satuan: "",
        harga: "",
        qty_fisik: "",
        keterangan_fisik: "",
      },
    ]);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!validateForm()) {
      CreateData();
    }
  };

  return (
    <div>
      <Row>
        <div className="col">
              <Card>
                <CardBody>
                  <Form onSubmit={handleSubmit}>
                    <CardHeader>
                      <h3>Buat Penawaran Sales Order</h3>
                    </CardHeader>
                    <CardBody>
                      <Row md="12">
                          <Col md="6">
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Kode SO
                              </Label>
                              <Col sm={7}>
                                <Input
                                  type="text"
                                  name="kode SO"
                                  placeholder="Masukan Kode SO"
                                  value={kodeso}
                                  onChange={(e) => {
                                    setKodeSo(e.target.value);
                                    
                                  }}
                                />
                                <Input
                                  type="hidden"
                                  name="username"
                                  placeholder="Masukan Keterangan PO"
                                  value={usernameSo}
                                  onChange={(e) => {
                                    setUsernameSo(e.target.value);
                                  }}
                                />
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                  Nama Customer
                              </Label>
                              <Col sm={7}>
                              <Input
                                  name="kategory"
                                  type="select"
                                  value={person}
                                  onChange={(e) => {
                                    setPerson(e.target.value);
                                  }}
                                >
                                  <option value=''>Pilih Customer</option>
                                  {persons.map((person, key) => {
                                        return (
                                        <option key={key} value={person.id}>
                                            {person.person_name}
                                        </option>
                                        );
                                    })}
                                  </Input>
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                keterangan SO
                              </Label>
                              <Col sm={7}>
                                <Input
                                  type="text"
                                  name="desc"
                                  placeholder="Masukan Keterangan SO"
                                  value={keteranganso}
                                  onChange={(e) => {
                                    setKeteranganSo(e.target.value);
                                    
                                  }}
                                />
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                  Tipe SO
                              </Label>
                              <Col sm={7}>
                              <Input
                                name="Tipe Po"
                                type="select"
                                value={soType}
                                onChange={(e) => {
                                  setSoType(e.target.value);
                                }}
                              >
                                <option value={1}>Ready</option>
                                <option value={2}>Indent</option>
                              </Input>
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Status SO
                              </Label>
                              <Col sm={7}>
                                <Input
                                  name="kategory"
                                  type="select"
                                  value={soStatus}
                                onChange={(e) => {
                                  setSoStatus(e.target.value);
                                }}
                              >
                                <option value={2}>Locked</option>
                                <option value={3}>Proses</option>
                                <option value={4}>Selesai</option>
                                <option value={5}>Gantung</option>
                                </Input>
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Metode Pembayaran
                              </Label>
                              <Col sm={7}>
                                  <Input
                                  name="metode Pembayaran"
                                  type="select"
                                  value={payment_method}
                                  onChange={(e) => {
                                    setPaymentMethod(e.target.value);
                                  }}
                                >
                                  <option value="">Pilih Metode Pembayaran</option>
                                  <option value="">Tunai</option>
                                  <option value="">Kredit</option>
                                  
                                </Input>
                              </Col>
                            </FormGroup>
                          </Col>
                          <Col md="6">
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Total Diskon
                              </Label>
                              <Col sm={7}>
                              <Input
                                  type="text"
                                  name="barcode"
                                  placeholder="Masukan Total Diskon"
                                  value={totaldiskon}
                                  onChange={(e) => {
                                    setTotalDiskon(e.target.value);
                                    
                                  }}
                                />
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                PPN
                              </Label>
                              <Col sm={7}>
                              <Input
                                  type="text"
                                  name="barcode"
                                  placeholder="Masukan Total PPN"
                                  value={ppn}
                                  onChange={(e) => {
                                    setPpn(e.target.value);
                                   
                                  }}
                                />
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Promo
                              </Label>
                              <Col sm={7}>
                                <Input
                                  type="text"
                                  name="desc"
                                  placeholder="Masukan Promo"
                                  value={promo}
                                  onChange={(e) => {
                                    setPromo(e.target.value);
                                                                     }}
                                />
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Jangka Waktu
                              </Label>
                              <Col sm={7}>
                              <Input
                                  type="text"
                                  name="jangka Waktu"
                                  placeholder="Masukan Jangka Waktu"
                                  value={jangkawaktu}
                                  onChange={(e) => {
                                    setJangkaWaktu(e.target.value);
                                  }}
                                />
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Termin
                              </Label>
                              <Col sm={7}>
                              <Input
                              name="cicil"
                              type="select"
                              value={cicil}
                              onChange={(e) => {
                                setCicil(e.target.value);
                              }}
                            >
                              <option value="">Pilih Cicilan</option>
                              <option value={1}>Lunas</option>
                              <option value={2}>Belum Lunas</option>
                              
                            </Input>
                              </Col>
                            </FormGroup>
                            
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Pengiriman
                              </Label>
                              <Col sm={7}>
                              <Input
                                  name="satuan"
                                  type="select"
                                  value={pengiriman}
                                  onChange={(e) => {
                                    setPengiriman(e.target.value);
                                  }}
                                >
                                  <option value={"DiKirim"}>DiKirim</option>
                                  <option value={"DiAntar"}>DiAntar</option>
                                </Input>
                              </Col>
                            </FormGroup>
                            
                          </Col>
                      </Row>
                      <Table>
                              <thead>
                                <tr>
                                  <th>
                                    <center>Kode Item</center>
                                  </th>
                                  <th>
                                    <center>Quantity</center>
                                  </th>
                                  <th>
                                    <center>Quantity Fisik</center>
                                  </th>
                                  <th>
                                    <center>Keterangan Fisik</center>
                                  </th>
                                  <th>
                                    <center>Aksi</center>
                                  </th>
                                </tr>
                              </thead>
                      </Table>
                      {inputList.map((x, i) => {
                        return (
                          <div key={i}>
                            <Table className="Table">
                              <tbody>
                                <tr>
                                  <td>
                                  <Input
                                      type="text"
                                      name="desc"
                                      placeholder="Masukan Item"
                                      value={kodeitem}
                                      onChange={(e) => {
                                        setKodeItem(e.target.value);
                                      
                                      }}
                                    />
                                  </td>
                                  <td>
                                  <Input
                                      type="text"
                                      name="desc"
                                      placeholder="Quantity"
                                      value={qty}
                                      onChange={(e) => {
                                        setQty(e.target.value);
                                      
                                      }}
                                    />
                                  </td>
                                  <td>
                                  <Input
                                      type="text"
                                      name="desc"
                                      placeholder="Masukan Quantity"
                                      value={qtyfisik}
                                      onChange={(e) => {
                                        setQtyFisik(e.target.value);
                                      
                                      }}
                                    />
                                  </td>
                                  <td>
                                  <Input
                                      type="text"
                                      name="desc"
                                      placeholder="Masukan Harga"
                                      value={keteranganfisik}
                                      onChange={(e) => {
                                        setKeteranganFisik(e.target.value);
                                      
                                      }}
                                    />
                                  </td>
                                  <td>
                                    
                                        {inputList.length !== 1 && (
                                          <Button
                                            color="primare"
                                            onClick={() => handleRemoveClick(i)}
                                          >
                                            <i className="fa fa-trash" aria-hidden="true"></i>
                                          </Button>
                                        )}
                                        {inputList.length - 1 === i && (
                                          <Button
                                            color="primare"
                                            onClick={handleAddClick}
                                          >
                                            <i className="fa fa-plus"></i>
                                          </Button>
                                        )}
                                  </td>
                                </tr>
                              </tbody>
                            </Table>
                          </div>
                        );  
                      })}
                    </CardBody>
                    <CardFooter >
                    <Row md="12">
                          <Col md="8">
                          </Col>
                          <Col md="4">
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Harga Total
                              </Label>
                              <Col sm={6}>
                              <Input
                                  type="text"
                                  name="barcode"
                                  placeholder="Harga Total"
                                  value={barcode}
                                  onChange={(e) => {
                                    setBarcode(e.target.value);
                                  }}
                                />
                              </Col>
                            </FormGroup>
                            
                          </Col>
                      </Row>
                    </CardFooter>
                  </Form>
                </CardBody>
                <CardFooter>
                  <Row md="12">
                    <Col md="9">
                    </Col>
                    <Col md="3">
                      {!isLoading && (
                        <Button color="primary" type="submit">
                          Simpan
                        </Button>
                      )}
                      {isLoading && (
                        <Button color="primary" disabled>
                          <i className="fas fa-spinner fa-spin"></i>
                          {""}
                          loading...
                        </Button>
                      )}
                      <Link className="btn btn-info" to="/admin/sales-order">
                        Kembali
                      </Link>
                    </Col>
                  </Row>
                </CardFooter>
              </Card>
          <Card>
            {/* body dari Input Item */}
            <CardHeader>
                <Row md="12">
                    <Col md="6">
                    </Col>
                    <Col md="6">
                        <FormGroup row>
                        <Col sm={9}>
                        </Col>
                        <Link className="btn btn-info" to="/admin/sales-order/create">
                            Buat SO
                        </Link>
                        </FormGroup>
                    </Col>
                </Row>
            </CardHeader>
            <CardBody>
            <h3>Data SO </h3>
              <ToolkitProvider
                rowNumber={rowIndex}
                data={allSo}
                keyField="id"
                columns={[
                  {
                    dataField: "no",
                    text: "#",
                    sort: true,
                    page: 1,
                    formatter: (cell, row, index) => {
                      let currentRow = ++index;
                      return currentRow + rowIndex;
                    },
                  },
                  {
                    dataField: "order_code",
                    text: "Kode SO",
                    sort: true,
                  },
                  {
                    dataField: "customer",
                    text: "Customer",
                    sort: true,
                  },
                  {
                    dataField: "satuan",
                    text: "Satuan",
                    sort: true,
                  },
                  {
                    dataField: "price_1",
                    text: "Harga ",
                    sort: true,
                  },
                  {
                    dataField: "",
                    text: "Aksi",
                    formatter: (cell, row, index) => {
                      return (
                        <ButtonGroup>
                          <Button>
                            <Link
                              to={redirectPrefix + row.id}
                              id={"tooltip_" + row.id}
                            >
                              <i className="fas fa-user-edit" />
                            </Link>
                          </Button>
                          <UncontrolledTooltip
                            delay={0}
                            target={"tooltip_" + row.id}
                          >
                            Edit
                          </UncontrolledTooltip>
                        </ButtonGroup>
                      );
                    },
                  },
                ]}
              >
                {(props) => (
                  <div className="py-4 table-responsive">
                    <BootstrapTable
                      remote
                      {...props.baseProps}
                      bootstrap4={true}
                      bordered={false}
                      hover={true}
                      pagination={paginationFactory({ ...paginationOption })}
                      onTableChange={handleTableChange}
                    />
                  </div>
                )}
              </ToolkitProvider>
            </CardBody>
          </Card>
        </div>
      </Row>
    </div>
    
  );
};

export default Index;
