/*eslint-disable*/
import React, { useEffect, useState } from "react";
// reactstrap components
import {
  Button,
  Card,
  CardBody,
  Input,
  InputGroupAddon,
  InputGroupText,
  Container,
  Row,
  Col,
  CarouselItem,
  CarouselCaption,
  Modal,
  ModalBody,
  FormFeedback,
} from "reactstrap";
import { useHistory } from "react-router-dom";
import { AvForm, AvGroup } from "availity-reactstrap-validation";
import axios from "axios";
// core components
import SweetAlert from "react-bootstrap-sweetalert";
import { encryptAES256, decryptAES256 } from "utils/AESchiper";

const items = [
  {
    src: require("assets/img/theme/das.png").default,
    caption: "Merchant Dashboard",
  },
  {
    src: require("assets/img/theme/edc.png").default,
    caption: "Payment Terminal",
  },
  {
    src: require("assets/img/theme/campur.png").default,
    caption: "Realtime Transaction Status",
  },
];

function Login() {
  let history = useHistory();
  const [email, setEmail] = useState("");
  const [emailError, setEmailError] = useState(null);
  const [wrongEmail, setWrongEmail] = useState(null);
  const [password, setPassword] = useState("");
  const [passwordError, setPasswordError] = useState(null);
  const [wrongPassword, setWrongPassword] = useState("");
  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);
  const [modal, setModal] = useState(false);
  const [passwordShown, setPasswordShown] = useState(false);
  const [alert, setAlert] = React.useState(null);
  const timestamp = Date.now();
  const [resetEmail, setResetEmail] = useState("");
  const [resetEmailError, setResetEmailError] = useState(null);
  const [isLoading, setLoading] = useState(false);
  console.log(timestamp);
  const [captchaResponse, setCaptchaResponse] = useState(null);
  const successCallback = (response) => {
    setCaptchaResponse(response);
  };
  const expiredCallback = () => setCaptchaResponse(null);
  const containerId = "test-recaptcha";
  // const sitekey = process.env.REACT_APP_GOOGLE_CAPTCHA;

  const [rememberMe, setRememberMe] = useState(false);
  const isEmail = localStorage.getItem("username");
  const isPassword = localStorage.getItem("isPassword");
  const cekRememberMe = localStorage.getItem("rememberMe");

  // useRecaptcha({
  //   containerId,
  //   successCallback,
  //   expiredCallback,
  //   size: "normal",
  //   sitekey,
  // });

  const validateForm = () => {
    let error = false;
    if (email === "") {
      setEmailError("invalid");
      error = true;
    }
    if (password === "") {
      setPasswordError("invalid");
      error = true;
    }
    return error;
  };

  const validateFormResetPassword = () => {
    let error = false;
    if (resetEmail === "") {
      setResetEmailError("invalid");
      error = true;
    }
    return error;
  };

  const togglePasswordVisiblity = () => {
    setPasswordShown(passwordShown ? false : true);
  };

  const toggle = () => setModal(!modal);

  const hideAlert = () => {
    setAlert(null);
  };

  const setMsgAlert = (str) => {
    setAlert(
      <SweetAlert
        error
        showConfirm
        confirmBtnText="Ok"
        title={str}
        onCancel={hideAlert}
        onConfirm={hideAlert}
      />
    );
  };

  const setSuccessAlert = () => {
    setAlert(
      <SweetAlert
        success
        showConfirm
        confirmBtnText="Ok"
        title="Link reset password sudah terkirim, silahkan cek email anda."
        onCancel={hideAlert}
        onConfirm={hideAlert}
      />
    );
  };

  const getMyProfil = async () => {
    const token = localStorage.token;
    
    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_BASE_URL}/info`,
        {
          method: "GET", // or 'PUT'
          headers: {
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify(),
        }
      );
      const data = await response.json();
      localStorage.setItem("userId", data.id);
      localStorage.setItem("email", data.email);
      localStorage.setItem("phoneNumber", data.phoneNumber);
      localStorage.setItem("username", data.username);
      localStorage.setItem("name", data.name);
      localStorage.setItem("authority", data.authorities[0].authority);
    } catch (error) {
      console.error("Error:", error);
    }
  };
  
  async function Login() {
    const key = email + timestamp;
    const emailwithLenght = email.length;
    const passwordLenght = password.length;
    let passwordWithLanght;
    if (passwordLenght < 13 ) {
      passwordWithLanght =  password + timestamp ;
    }  else {
      passwordWithLanght = password + timestamp + "0";
    };

    let emailwithLanght;
    if (emailwithLenght < 13 ) {
      emailwithLanght = email + timestamp ;
    } else {
      emailwithLanght = email + timestamp + "0";
    }

    let body = {
      usernameOrEmail: await encryptAES256(emailwithLanght),
      password: await encryptAES256(passwordWithLanght),
    };

    axios
      .post(
        `${process.env.REACT_APP_API_BASE_URL}/users/mobile/login`,
        body
      )
      // axios.post(`${process.env.REACT_APP_API_BASE_URL}/users/login?g-recaptcha-response=${captchaResponse}&remember-me=${rememberMe}`, body)
      .then(function (response) {
        console.log(body);
        if (response.data.error) {
          showErrors(response.data.error);
          return;
        }
        if (response.data.errors) {
          showErrorsEmail(response.data.errors);
          return;
        }
        // setPassword(localStorage.setItem("isPassword", password));
        window.localStorage.setItem("token", response.data.response.token);
        // window.localStorage.setItem("rememberMe", response.data.response_data.remember_me);
        // window.localStorage.setItem("refreshToken", response.data.response_data.refreshToken);
        getMyProfil();
        history.push("/admin/dashboard");
      })
      .catch(function (error) {
        setMsgAlert(error.message);
        console.log(error);
      });
  }

  const showErrorsEmail = (error) => {
    setEmailError("invalid");
    setWrongEmail(error.usernameOrEmail);
  };

  const showErrors = (error) => {
    setEmailError("invalid");
    setWrongEmail(error.usernameOrEmail);
    setPasswordError("invalid");
    setWrongPassword(error.password);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!validateForm()) {
      Login();
    }
  };

  useEffect(() => {
    getRememberMe();
  }, []);

  const getRememberMe = () => {
    if (cekRememberMe === "true") {
      setEmail(isEmail);
      setPassword(isPassword);
      setRememberMe(true);
    } else {
      setEmail("");
      setPassword("");
    }
  };

  function ForgotPassword() {
    setLoading(true);
    let body = {
      email: resetEmail,
    };
    axios
      .post(`${process.env.REACT_APP_API_BASE_URL}/user/forgot-password`, body)
      .then(function (response) {
        setSuccessAlert();
        setResetEmail("");
        history.push("/auth/login");
      })
      .then((json) => {
        setLoading(false);
      })
      .catch(function (error) {
        console.log(error);
      });
  }

  const handleSubmitForgotPassword = (e) => {
    e.preventDefault();
    if (!validateFormResetPassword()) {
      ForgotPassword();
    }
  };

  return (
    <>
      {alert}
      <Container className="mt--8 pb-5 login-container">
        <Row className="justify-content-center">
          <Col lg="5" md="7">
            <Card className="bg-secondary border-0 mb-0">
              <img
                alt="login"
                style={{ width: "100%" }}
                src={require("assets/img/theme/Hokky1.png").default}
              />
              <CardBody className="px-lg-5 py-lg-5">
                <p>Wellcome back, Please Login to your Account.</p>
                <AvForm onSubmit={handleSubmit}>
                  <AvGroup className="input-group">
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <i className="ni ni-email-83" />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      placeholder="Masukan Email / Username"
                      value={email}
                      invalid={emailError === "invalid"}
                      onChange={(e) => {
                        setEmail(e.target.value);
                        if (e.target.value !== "") {
                          setEmailError("");
                        }
                      }}
                    />
                    <FormFeedback>
                      {email === ""
                        ? "Email atau Username tidak boleh kosong"
                        : wrongEmail}
                    </FormFeedback>
                  </AvGroup>
                  <AvGroup className="input-group">
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <i className="ni ni-lock-circle-open" />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      placeholder="Masukkan Kata Sandi"
                      type={passwordShown ? "text" : "password"}
                      value={password}
                      invalid={passwordError === "invalid"}
                      onChange={(e) => {
                        setPassword(e.target.value);
                        if (e.target.value !== "") {
                          setPasswordError("");
                        }
                      }}
                    />
                    <InputGroupText>
                      {passwordShown !== true ? (
                        <i
                          className="fa fa-eye"
                          aria-hidden="true"
                          onClick={togglePasswordVisiblity}
                        />
                      ) : (
                        <i
                          className="fa fa-eye-slash"
                          aria-hidden="true"
                          onClick={togglePasswordVisiblity}
                        />
                      )}
                    </InputGroupText>
                    <FormFeedback>
                      {password === ""
                        ? "Password tidak boleh kosong"
                        : wrongPassword}
                    </FormFeedback>
                  </AvGroup>
                  <Row>
                    <Col md={6}>
                      <div className="custom-control custom-control-alternative custom-checkbox">
                        <input
                          className="custom-control-input"
                          id=" customCheckLogin"
                          type="checkbox"
                          checked={rememberMe}
                          value={true}
                          onChange={(e) => {
                            setRememberMe(e.target.checked);
                          }}
                        />
                        <label
                          className="custom-control-label"
                          htmlFor=" customCheckLogin"
                        >
                          <span className="text-muted">Ingatkan saya</span>
                        </label>
                      </div>
                    </Col>
                    <Col md={6} style={{ textAlign: "right" }}>
                      <a className="text-light" onClick={toggle}>
                        <small>Lupa Kata Sandi?</small>
                      </a>
                    </Col>
                    <Modal
                      isOpen={modal}
                      toggle={toggle}
                      style={{ textAlign: "center" }}
                    >
                      <ModalBody>
                        <img
                          alt="login"
                          style={{ width: "24%", marginBottom: "15px" }}
                          src={require("assets/img/brand/Hokky1.png").default}
                        />
                        <h3>Lupa Kata Sandi ?</h3>
                        <p>
                          Enter email address and we will send you instructions
                          to reset Password.
                        </p>
                        <AvGroup className="input-group">
                          <InputGroupAddon addonType="prepend">
                            <InputGroupText>
                              <i className="ni ni-email-83" />
                            </InputGroupText>
                          </InputGroupAddon>
                          <Input
                            placeholder="Masukan Email / Username"
                            value={resetEmail}
                            invalid={resetEmailError === "invalid"}
                            onChange={(e) => {
                              setResetEmail(e.target.value);
                              if (e.target.value !== "") {
                                setResetEmailError("");
                              }
                            }}
                          />
                          <FormFeedback>
                            Email atau Username tidak boleh kosong
                          </FormFeedback>
                        </AvGroup>
                        <div classname="text-center">
                          {!isLoading && (
                            <Button
                              onClick={handleSubmitForgotPassword}
                              type="button"
                              color="primary"
                            >
                              Send
                            </Button>
                          )}
                          {isLoading && (
                            <Button color="primary" disabled>
                              <i className="fas fa-spinner fa-spin"></i>
                              {""}
                              Loading...
                            </Button>
                          )}
                        </div>
                      </ModalBody>
                    </Modal>
                  </Row>
                  <div id={containerId} className="g-recaptcha" />
                  <div className="text-center">
                    {/* <Button className="my-4" disabled={!captchaResponse} color="info" type="submit"> */}
                    <Button className="my-4" color="info" type="submit">
                      Masuk
                    </Button>
                  </div>
                </AvForm>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </>
  );
}

export default Login;
