/*eslint-disable*/
import React, { useEffect, useState } from "react";
import {
  
  Card,
  Button,
  Row,
  Col,
  CardBody,
  CardHeader,
  CardFooter,
  Table,
  ButtonGroup,
  Container,
  Form,
  FormGroup,
  UncontrolledTooltip,
  Label,
  Input,
  
} from "reactstrap";
import { Link,useHistory } from "react-router-dom";
import axios from "axios";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import SimpleHeader from "components/Headers/SimpleHeader.js";

const CreatePermintaanBarang = () => {
  const token = localStorage.token;
  const username = localStorage.username;
  const warehouse = localStorage.warehouse;
  let history = useHistory();
  
  // const warehouse = parseInt(localStorage.warehouse);
  const [activeTab, setActiveTab] = useState("1");
  const [alert, setAlert] = React.useState(null);
  const [rowIndex, setRowIndex] = useState(0);
  const redirectPrefix = `/admin/item/edit/`;
  const [isOpen, setIsOpen] = useState(false);
  const toggleOpen = () => setIsOpen(!isOpen);
  const [page, setPage] = useState(1);
  const [hargatotal,setHargaTotal] = useState("");
  const [perPage, setPerpage] = useState(10);
  const [totalItem, setTotalItem] = useState(0);
  const [currentSort, setCurrentSort] = useState("");
  const [nameItem, setNameItem] = useState("");
  const [satuan, setSatuan] = useState("");
  const [allSatuan, setAllSatuan] = useState([]);
  const [input, setInput] = useState("");
  const [savedItems, setSavedItems] = useState([]);
  const [allPo, setAllPo] = useState([]);
  const [isLoading, setLoading] = useState(false);
  const [usernamePo, setUsernamePo] = useState(username);
  const [Item_Code,setItemCode] = useState("");
  const [qty,setQty] = useState([]);
  const [harga, setHarga] = useState("");
  const [typereq, setTypeReq] = useState([]);
  const [persons, setPersons] = useState([]);
  const [person, setPerson] = useState("");
  const [payment_method,setPaymentMethod] = useState([]);
  const [durasipo,setDurasiPo] = useState("");
  const [descriptionPayment,setDescriptionPayment] = useState("");
  const [keterangan,setKeterangan] = useState("");
  const [isShow, setIsShow] = useState(false);
  const [jangkaWaktu,setJangkaWaktu] = useState([]);
  const [is_cicil,setIsCicil] = useState([]);
  const [quantity, setQuantity] = useState(0);
  const [active, setActive] = useState(0);
  const [filtered, setFiltered] = useState([]);
  const [totalPrice, setTotalPrice] = useState(0);

  const [inputList, setInputList] = useState([
    {
      item_id: "",
      qty: "",
      satuan: "",
      harga: "",
      qty_fisik: "",
      keterangan_fisik: "",
    },
  ]);

  useEffect(() => {
    getPerson();
    getSatuan();

  }, []);

  const getPerson = () => {
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    axios
      .get(
        `${process.env.REACT_APP_API_BASE_URL}/person/list`,
        { headers }
      )
      .then((data) => {
        setPersons(data.data.response);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const getSatuan = () => {
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    axios
      .get(`${process.env.REACT_APP_API_BASE_URL}/uom/list`, { headers })
      .then((data) => {
        setAllSatuan(data.data.response);
      })
      .catch(function (error) {
        console.log(error);
      });
  };


  function CreateData() {
    setLoading(true);
    
   }

 
  let paginationOption = {
    page: page,
    alwaysShowAllBtns: true,
    override: true,
    showTotal: true,
    withFirstAndLast: false,
    sizePerPage: perPage,
    totalSize: totalItem,
    onPageChange: (page) => {
      updateDataTable(page, perPage, currentSort, nameItem);
    },
    sizePerPageRenderer: () => (
      <div className="dataTables_length" id="datatable-basic_length">
        <label>
          Show{" "}
          {
            <select
              name="datatable-basic_length"
              aria-controls="datatable-basic"
              className="form-control form-control-sm"
              onChange={(e) => {
                updateDataTable(page, e.target.value, currentSort);
              }}
            >
              <option value="10">10</option>
              <option value="20">20</option>
              <option value="25">25</option>
              <option value="50">50</option>
            </select>
          }{" "}
          entries.
        </label>
      </div>
    ),
  };

  const updateDataTable = (page, perPage, sort, nameItem) => {
    getItem(page, perPage, sort, nameItem);
    setPage(page);
    setPerpage(perPage);
    setRowIndex((page - 1) * perPage);
    setCurrentSort(sort);
    setNameItem(nameItem);
    getPrice(page, perPage, sort);
  };

  const handleTableChange = (type, { sortField, sortOrder }) => {
    if (type === "sort") {
      let sort = `${sortField} ${sortOrder}`;
      updateDataTable(page, perPage, sort, nameItem);
    }
  };

  const toggle = (tab) => {
    if (activeTab !== tab) setActiveTab(tab);
  };

  // ambil data dari harga

  useEffect(() => {
    getPo(page, perPage, currentSort);
  }, []);

  // fungsi dari ambil data
  const getPo = (page, perPage, currentSort) => {
    
    let filter = {
       page: page,
       per_page: perPage, 
      };

    const data = filter;
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    axios
      .post(`${process.env.REACT_APP_API_BASE_URL}/purchase-order/page`, data, {
        headers,
      })
      .then((data) => {
        setAllPo(data.data.response);
        setPage(data.data.current_page + 1);
        setPerpage(data.data.per_page);
        setTotalItem(data.data.total_item);
      })
      .catch(function (error) {
        console.log(error);
      });
  };


  // handle click event of the Remove button
  const handleRemoveClick = (index) => {
    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
  };

  // handle click event of the Add button
  const handleAddClick = () => {
    setInputList([
      ...inputList,
      {
        item_id: "",
        qty: "",
        satuan: "",
        harga: "",
        qty_fisik: "",
        keterangan_fisik: "",
      },
    ]);
  };

  function CreateData() {
    setLoading(true);
    let data = {
      warehouse_id : parseInt(warehouse),
      username_po : "",
      payment_method : [],
      jangka_waktu: parseInt(jangkaWaktu),
      keterangan_payment: descriptionPayment,
      is_cicil: parseInt(is_cicil),
      person: parseInt(person),
      po_type : poType,
      durasi_po: durasipo,
      keterangan_po : descriptionPo ,
      approve : 0,
      username_admin: "",
      admin_approval: 0,
      keterangan_admin: "",
      username_validator : "",
      clear : 0,
      keterangan_validator : "" ,
      username_gudang : "",
      status_barang : 0,
      keterangan_gudang : "",
      username_kurir : "",
      
      items : []
      
    };
    axios
        .post(
          `${process.env.REACT_APP_API_BASE_URL}/purchase-order/save`,
          data,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        )
        .then(function (response) {
          history.push("/admin/po");
        })
        .then((json) => {
          setLoading(false);
        })
        .catch(function (error) {
          console.log(error);
        });
  }

  const saveItem = () => {
    if (quantity === '' || quantity <= 0)
        return;

    // let haveSameData = savedItems.find(object => object.item_id === selectedItem.value);
    // if (haveSameData !== undefined)
    //     return;

    axios.post(`${process.env.REACT_APP_API_BASE_URL}/items`, {
        page: 1,
        per_page: 1,
        name: input
    }).then(async response => {
        const length = response.data.response.length;
        if (length === 0)
            return;

        const idItem = response.data.response[0].id;

        axios.get(`${process.env.REACT_APP_API_BASE_URL}/items/${idItem}`)
            .then(async response => {
                return {
                    item: response.data.response,
                    satuan: satuan
                };
            }).then((data) => {
                let stateItem = [...savedItems, {
                    item_id: idItem,
                    qty: parseInt(quantity),
                }];

                setTotalPrice(totalPrice + data.item.harga * parseInt(quantity));
                setSavedItems(stateItem);
            });
    });
}

const onChange = e => {
    const input = e.currentTarget.value;

    axios.post(`${process.env.REACT_APP_API_BASE_URL}/items`, {
        page: 1,
        per_page: 10,
        name: input
    }).then(async response => {
        let suggests = [];

        await Promise.all(response.data.response.map((data) =>
            suggests = [...suggests, data.item_name]
        ));

        setActive(0);
        setFiltered(suggests);
        setIsShow(true);
    });

    setInput(e.currentTarget.value);
};
const onClick = e => {
    setActive(0);
    setFiltered([]);
    setIsShow(false);
    setInput(e.currentTarget.innerText)
};
const onKeyDown = e => {
    if (e.keyCode === 13) { // enter key
        setActive(0);
        setIsShow(false);
        setInput(filtered[active])
    }
    else if (e.keyCode === 38) { // up arrow
        return (active === 0) ? null : setActive(active - 1);
    }
    else if (e.keyCode === 40) { // down arrow
        return (active - 1 === filtered.length) ? null : setActive(active + 1);
    }
};

const AutoCompleTes = () => {
    if (isShow && input) {
        if (filtered.length) {
            return (
                <ul className="autocomplete">
                    {filtered.map((suggestion, index) => {
                        let className;
                        if (index === active) {
                            className = "active";
                        }
                        return (
                            <li key={index} className={className} onClick={onClick}>
                                {suggestion}
                            </li>
                        );
                    })}
                </ul>
            );
        } else {
            return (
                <div className="no-autocomplete">
                    <em>Not found</em>
                </div>
            );
        }
    }
    return <></>;
}
  
  const handleSubmit = (e) => {
    e.preventDefault();
    {
      CreateData();
    }
  };

  return (
    <>
    <SimpleHeader name="Create Permintaan Barang" parentName="PO" />
    <Container className="mt--6" fluid>
        <Row>
          <div className="col">
              <Card>
                <Form onSubmit={handleSubmit}>
                <CardBody>
                    <CardHeader>
                      <h3>Daftar Purchase Order</h3>
                    </CardHeader>
                    <CardBody>
                      <Row md="12">
                          <Col md="6">
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Supplier
                              </Label>
                              <Col sm={7}>
                              <Input
                                  name="person"
                                  type="select"
                                  value={person}
                                  onChange={(e) => {
                                    setPerson(e.target.value);
                                  }}
                                >
                                  <option value=''>Pilih Supplier</option>
                                  {persons.map((person, key) => {
                                      return (
                                        <option key={key} value={person.id}>
                                          {person.person_name}
                                        </option>
                                      );
                                    })}
                                  </Input>
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Keterangan
                              </Label>
                              <Col sm={7}>
                                <Input
                                  type="text"
                                  name="desc"
                                  placeholder="Masukan Keterangan"
                                  value={keterangan}
                                  onChange={(e) => {
                                    setKeterangan(e.target.value);
                                  }}
                                />
                              </Col>                             
                            </FormGroup>
                            <FormGroup row>
                            <Label
                              for="exampleEmail"
                              sm={5}
                            >
                              Tipe Request
                            </Label>
                            <Col sm={7}>
                            <Input
                                name="Tipe Po"
                                type="select"
                                value={typereq}
                                onChange={(e) => {
                                  setTypeReq(e.target.value);
                                }}
                              >
                                <option value={1}>Customer Request</option>
                                <option value={2}>Stok Request</option>
                              </Input>
                            </Col>
                            </FormGroup>
                          </Col>
                          <Col md="6">
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Durasi PO
                              </Label>
                              <Col sm={7}>
                              <Input
                                  type="text"
                                  name="barcode"
                                  placeholder="Masukan Durasi Tempo"
                                  value={durasipo}
                                  onChange={(e) => {
                                    setDurasiPo(e.target.value);
                                  }}
                                />
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Jangka Waktu
                              </Label>
                              <Col sm={7}>
                              <Input
                                  type="number"
                                  name="jangka waktu"
                                  placeholder="Masukan Jangka Waktu"
                                  value={jangkaWaktu}
                                  onChange={(e) => {
                                    setJangkaWaktu(e.target.value);
                                  }}
                                />
                              </Col>
                            </FormGroup>
                            
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Cicilan
                              </Label>
                              <Col sm={7}>
                              <Input
                              name="satuan"
                              type="select"
                              value={is_cicil}
                              onChange={(e) => {
                                setSatuan(e.target.value);
                              }}
                            >
                              <option value={1}>Lunas</option>
                              <option value={2}>Belum Lunas</option>
                            </Input>
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Nominal Cicilan
                              </Label>
                              <Col sm={7}>
                                <Input
                                  type="text"
                                  name="desc"
                                  placeholder="Masukan Nominal Cicilan"
                                  value={is_cicil}
                                  onChange={(e) => {
                                    setIsCicil(e.target.value);
                                  }}
                                />
                              </Col>
                            </FormGroup>
                          </Col>
                      </Row>
                      <Col xs="12">
                                        <hr />
                                    </Col>

                                    <Col xl="10">
                                        <FormGroup>
                                            <Label>
                                                Item
                                            </Label>
                                            {/* <Select options={items} placeholder="Item ..." onChange={(e) => console.log(e)} /> */}
                                            <Input
                                                placeholder="Item ..."
                                                type="text"
                                                style={{ height: 38 }}
                                                onChange={onChange}
                                                onKeyDown={onKeyDown}
                                                value={input}
                                            />
                                            <AutoCompleTes />
                                        </FormGroup>
                                    </Col>
                                    <Col xl="2">
                                        <FormGroup>
                                            <Label>
                                                Quantity
                                            </Label>
                                            <Input
                                                placeholder="Quantity"
                                                type="number"
                                                style={{ height: 38 }}
                                                onChange={(e) => setQuantity(e.target.value)}
                                            />

                                        </FormGroup>
                                    </Col>
                                    <Col xl="12">
                                        <FormGroup>
                                            <Label>
                                                Deskripsi
                                            </Label>
                                            <Input
                                                placeholder="Deskripsi"
                                                type="textarea"
                                                rows="2"
                                                onChange={(e) => setDescription(e.target.value)}
                                            />
                                        </FormGroup>
                                    </Col>
                                    <Col xl="12">
                                        <Button color="primary" className="mb-3" onClick={() => saveItem()}>Add</Button>
                                    </Col>
                      <Table>
                              <thead>
                                <tr>
                                  <th>
                                    <center>Nama Item</center>
                                  </th>
                                  <th>
                                    <center>Kode Item</center>
                                  </th>
                                  <th>
                                    <center>Quantity</center>
                                  </th>
                                  <th>
                                    <center>Satuan</center>
                                  </th>
                                  <th>
                                    <center>Harga</center>
                                  </th>
                                  <th>
                                    <center>Aksi</center>
                                  </th>
                                </tr>
                              </thead>
                              <tbody>
                                {
                                    savedItems.map((savedItem, key) => {
                                        return (
                                            <tr key={key}>
                                                <td>{savedItem.data.name}</td>
                                                <td>{savedItem.satuan.name}</td>
                                                <td>{savedItem.qty}</td>
                                                <td>{formatRupiah(savedItem.data.harga)}</td>
                                                <td>{formatRupiah(savedItem.data.harga * savedItem.qty)}</td>
                                                <td>
                                                    <Button color="danger" onClick={() => deleteItem(savedItem.item_id)}>Delete</Button>
                                                </td>
                                            </tr>
                                        )
                                    })
                                }
                              </tbody>
                      </Table>
                    </CardBody>
                    <CardFooter >
                    <Row md="12">
                          <Col md="8">
                          </Col>
                          <Col md="4">
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Harga Total
                              </Label>
                              <Col sm={6}>
                              <Input
                                  type="text"
                                  name="barcode"
                                  placeholder="Harga Total"
                                  value={hargatotal}
                                  onChange={(e) => {
                                    setHargaTotal(e.target.value);
                                  }}
                                />
                              </Col>
                            </FormGroup>
                          </Col>
                      </Row>
                    </CardFooter>
                </CardBody>
                <CardFooter>
                      {!isLoading && (
                        <Button color="primary" type="submit">
                          Simpan
                        </Button>
                      )}
                      {isLoading && (
                        <Button color="primary" disabled>
                          <i className="fas fa-spinner fa-spin"></i>
                          {""}
                          loading...
                        </Button>
                      )}
                      <Link className="btn btn-info" to="/admin/po">
                        Kembali
                      </Link>
                </CardFooter>
                </Form>
              </Card>
          </div>
        </Row>
    </Container>  
    </>
  );
}

export default CreatePermintaanBarang;