/*eslint-disable*/
import React, { useEffect, useState } from "react";
import {
  
  Card,
  Button,
  Row,
  Col,
  CardBody,
  CardHeader,
  CardFooter,
  Table,
  ButtonGroup,
  Form,
  FormGroup,
  UncontrolledTooltip,
  Label,
  Input,
  
} from "reactstrap";
import { Link,useHistory } from "react-router-dom";
import axios from "axios";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";

const PenawaranPo = () => {
  const token = localStorage.token;
  const username = localStorage.username;
  const warehouse = localStorage.warehouse;
  let history = useHistory();
  
  // const warehouse = parseInt(localStorage.warehouse);
  const [activeTab, setActiveTab] = useState("1");
  const [alert, setAlert] = React.useState(null);
  const [rowIndex, setRowIndex] = useState(0);
  const redirectPrefix = `/admin/item/edit/`;
  const [isOpen, setIsOpen] = useState(false);
  const toggleOpen = () => setIsOpen(!isOpen);
  const [page, setPage] = useState(1);
  const [hargatotal,setHargaTotal] = useState("");
  const [perPage, setPerpage] = useState(10);
  const [totalItem, setTotalItem] = useState(0);
  const [currentSort, setCurrentSort] = useState("");
  const [nameItem, setNameItem] = useState("");
  const [satuan, setSatuan] = useState("");
  const [allSatuan, setAllSatuan] = useState([]);
  const [allPo, setAllPo] = useState([]);
  const [isLoading, setLoading] = useState(false);
  const [usernamePo, setUsernamePo] = useState(username);
  const [Item_Code,setItemCode] = useState("");
  const [qty,setQty] = useState([]);
  const [harga, setHarga] = useState("");
  const [poType, setPoType] = useState("");
  const [persons, setPersons] = useState([]);
  const [person, setPerson] = useState("");
  const [payment_method,setPaymentMethod] = useState([]);
  const [durasipo,setDurasiPo] = useState("");
  const [descriptionPayment,setDescriptionPayment] = useState("");
  const [descriptionPo,setDescriptionPo] = useState("");
  const [jangkaWaktu,setJangkaWaktu] = useState([]);
  const [is_cicil,setIsCicil] = useState([]);

  const [inputList, setInputList] = useState([
    {
      item_id: "",
      qty: "",
      satuan: "",
      harga: "",
      qty_fisik: "",
      keterangan_fisik: "",
    },
  ]);

  useEffect(() => {
    getPerson();
    getSatuan();

  }, []);

  const getPerson = () => {
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    axios
      .get(
        `${process.env.REACT_APP_API_BASE_URL}/person/list`,
        { headers }
      )
      .then((data) => {
        setPersons(data.data.response);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const getSatuan = () => {
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    axios
      .get(`${process.env.REACT_APP_API_BASE_URL}/uom/list`, { headers })
      .then((data) => {
        setAllSatuan(data.data.response);
      })
      .catch(function (error) {
        console.log(error);
      });
  };


  function CreateData() {
    setLoading(true);
    
   }

 
  let paginationOption = {
    page: page,
    alwaysShowAllBtns: true,
    override: true,
    showTotal: true,
    withFirstAndLast: false,
    sizePerPage: perPage,
    totalSize: totalItem,
    onPageChange: (page) => {
      updateDataTable(page, perPage, currentSort, nameItem);
    },
    sizePerPageRenderer: () => (
      <div className="dataTables_length" id="datatable-basic_length">
        <label>
          Show{" "}
          {
            <select
              name="datatable-basic_length"
              aria-controls="datatable-basic"
              className="form-control form-control-sm"
              onChange={(e) => {
                updateDataTable(page, e.target.value, currentSort);
              }}
            >
              <option value="10">10</option>
              <option value="20">20</option>
              <option value="25">25</option>
              <option value="50">50</option>
            </select>
          }{" "}
          entries.
        </label>
      </div>
    ),
  };

  const updateDataTable = (page, perPage, sort, nameItem) => {
    getItem(page, perPage, sort, nameItem);
    setPage(page);
    setPerpage(perPage);
    setRowIndex((page - 1) * perPage);
    setCurrentSort(sort);
    setNameItem(nameItem);
    getPrice(page, perPage, sort);
  };

  const handleTableChange = (type, { sortField, sortOrder }) => {
    if (type === "sort") {
      let sort = `${sortField} ${sortOrder}`;
      updateDataTable(page, perPage, sort, nameItem);
    }
  };

  const toggle = (tab) => {
    if (activeTab !== tab) setActiveTab(tab);
  };

  // ambil data dari harga

  useEffect(() => {
    getPo(page, perPage, currentSort);
  }, []);

  // fungsi dari ambil data
  const getPo = (page, perPage, currentSort) => {
    
    let filter = {
       page: page,
       per_page: perPage, 
      };

    const data = filter;
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    axios
      .post(`${process.env.REACT_APP_API_BASE_URL}/purchase-order/page`, data, {
        headers,
      })
      .then((data) => {
        setAllPo(data.data.response);
        setPage(data.data.current_page + 1);
        setPerpage(data.data.per_page);
        setTotalItem(data.data.total_item);
      })
      .catch(function (error) {
        console.log(error);
      });
  };


  // handle click event of the Remove button
  const handleRemoveClick = (index) => {
    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
  };

  // handle click event of the Add button
  const handleAddClick = () => {
    setInputList([
      ...inputList,
      {
        item_id: "",
        qty: "",
        satuan: "",
        harga: "",
        qty_fisik: "",
        keterangan_fisik: "",
      },
    ]);
  };

  function CreateData() {
    setLoading(true);
    let data = {
      warehouse_id : parseInt(warehouse),
      username_po : "",
      payment_method : [],
      jangka_waktu: parseInt(jangkaWaktu),
      keterangan_payment: descriptionPayment,
      is_cicil: parseInt(is_cicil),
      person: parseInt(person),
      po_type : poType,
      durasi_po: durasipo,
      keterangan_po : descriptionPo ,
      approve : 0,
      username_admin: "",
      admin_approval: 0,
      keterangan_admin: "",
      username_validator : "",
      clear : 0,
      keterangan_validator : "" ,
      username_gudang : "",
      status_barang : 0,
      keterangan_gudang : "",
      username_kurir : "",
      
      items : []
      
    };
    axios
        .post(
          `${process.env.REACT_APP_API_BASE_URL}/purchase-order/save`,
          data,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        )
        .then(function (response) {
          history.push("/admin/po");
        })
        .then((json) => {
          setLoading(false);
        })
        .catch(function (error) {
          console.log(error);
        });
  }
  
  const handleSubmit = (e) => {
    e.preventDefault();
    {
      CreateData();
    }
  };

  return (
    <>
        <Row>
          <div className="col">
              <Card>
                <Form onSubmit={handleSubmit}>
                <CardBody>
                    <CardHeader>
                      <h3>Daftar Purchase Order</h3>
                    </CardHeader>
                    <CardBody>
                      <Row md="12">
                          <Col md="6">
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Supplier
                              </Label>
                              <Col sm={7}>
                              <Input
                                  name="person"
                                  type="select"
                                  value={person}
                                  onChange={(e) => {
                                    setPerson(e.target.value);
                                  }}
                                >
                                  <option value=''>Pilih Supplier</option>
                                  {persons.map((person, key) => {
                                      return (
                                        <option key={key} value={person.id}>
                                          {person.person_name}
                                        </option>
                                      );
                                    })}
                                  </Input>
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Keterangan PO
                              </Label>
                              <Col sm={7}>
                                <Input
                                  type="text"
                                  name="desc"
                                  placeholder="Masukan Keterangan PO"
                                  value={descriptionPo}
                                  onChange={(e) => {
                                    setDescriptionPo(e.target.value);
                                  }}
                                />
                              </Col>                             
                            </FormGroup>
                            <FormGroup row>
                            <Label
                              for="exampleEmail"
                              sm={5}
                            >
                              Tipe PO
                            </Label>
                            <Col sm={7}>
                            <Input
                                name="Tipe Po"
                                type="select"
                                value={poType}
                                onChange={(e) => {
                                  setPoType(e.target.value);
                                }}
                              >
                                <option value={1}>Ready</option>
                                <option value={2}>Indent</option>
                              </Input>
                            </Col>
                          </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Metode Pembayaran
                              </Label>
                              <Col sm={7}>
                                  <Input                                 
                                  name="satuan"
                                  type="select"
                                  value={payment_method}
                                  onChange={(e) => {
                                    setPaymentMethod(e.target.value);
                                  }}
                                >
                                  <option value={1}>Tunai/Transfer</option>
                                  <option value={2}>Termin/Tempo</option>
                                  <option value={3}>Deposito</option>
                                </Input>
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Keterangan Pembayaran
                              </Label>
                              <Col sm={7}>
                                  <Input                                 
                                  name="satuan"
                                  type="select"
                                  value={descriptionPayment}
                                  onChange={(e) => {
                                    setDescriptionPayment(e.target.value);
                                  }}
                                >
                                  <option value={1}>Tunai/Transfer</option>
                                  <option value={2}>Termin/Tempo</option>
                                  <option value={3}>Deposito</option>
                                </Input>
                              </Col>
                            </FormGroup>
                          </Col>
                          <Col md="6">
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Durasi PO
                              </Label>
                              <Col sm={7}>
                              <Input
                                  type="text"
                                  name="barcode"
                                  placeholder="Masukan Durasi Tempo"
                                  value={durasipo}
                                  onChange={(e) => {
                                    setDurasiPo(e.target.value);
                                  }}
                                />
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Jangka Waktu
                              </Label>
                              <Col sm={7}>
                              <Input
                                  type="number"
                                  name="jangka waktu"
                                  placeholder="Masukan Jangka Waktu"
                                  value={jangkaWaktu}
                                  onChange={(e) => {
                                    setJangkaWaktu(e.target.value);
                                  }}
                                />
                              </Col>
                            </FormGroup>
                            
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Cicilan
                              </Label>
                              <Col sm={7}>
                              <Input
                              name="satuan"
                              type="select"
                              value={is_cicil}
                              onChange={(e) => {
                                setSatuan(e.target.value);
                              }}
                            >
                              <option value={1}>Lunas</option>
                              <option value={2}>Belum Lunas</option>
                            </Input>
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={5}
                              >
                                Nominal Cicilan
                              </Label>
                              <Col sm={7}>
                                <Input
                                  type="text"
                                  name="desc"
                                  placeholder="Masukan Nominal Cicilan"
                                  value={is_cicil}
                                  onChange={(e) => {
                                    setIsCicil(e.target.value);
                                  }}
                                />
                              </Col>
                            </FormGroup>
                          </Col>
                      </Row>
                      <Table>
                              <thead>
                                <tr>
                                  <th>
                                    <center>Nama Item</center>
                                  </th>
                                  <th>
                                    <center>Kode Item</center>
                                  </th>
                                  <th>
                                    <center>Quantity</center>
                                  </th>
                                  <th>
                                    <center>Satuan</center>
                                  </th>
                                  <th>
                                    <center>Harga</center>
                                  </th>
                                  <th>
                                    <center>Aksi</center>
                                  </th>
                                </tr>
                              </thead>
                              <tbody>
                            </tbody>
                      </Table>
                      {inputList.map((x, i) => {
                        return (
                          <div key={i}>
                            <Table className="Table">
                              <tbody>
                                <tr>
                                  <td>
                                  <Input
                                      type="text"
                                      name="desc"
                                      placeholder="Nama Item"
                                      value={nameItem}
                                      onChange={(e) => {
                                        setNameItem(e.target.value);
                                      }}
                                    />
                                  </td>
                                  <td>
                                  <Input
                                      type="text"
                                      name="desc"
                                      placeholder="Kode Item"
                                      value={Item_Code}
                                      onChange={(e) => {
                                        setItemCode(e.target.value);
                                      }}
                                    />
                                  </td>
                                  <td>
                                  <Input
                                      type="text"
                                      name="desc"
                                      placeholder="Masukan Quantity"
                                      value={qty}
                                      onChange={(e) => {
                                        setQty(e.target.value);
                                      }}
                                    />
                                  </td>
                                  <td>
                                  <Input
                                      name="Pilih Satuan"
                                      type="select"
                                      value={satuan}
                                      onChange={(e) => {
                                      setSatuan(e.target.value);
                                      }}
                                  >
                                      <option value="">Pilih Satuan</option>
                                      {allSatuan.map((s, key) => {
                                      return (
                                          <option key={key} value={s.name}>
                                          {s.description}
                                          </option>
                                      );
                                      })}
                                  </Input>
                                  </td>
                                  <td>
                                  <Input
                                      type="text"
                                      name="desc"
                                      placeholder="Masukan Harga"
                                      value={harga}
                                      onChange={(e) => {
                                        setHarga(e.target.value);
                                      }}
                                    />
                                  </td>
                                  <td>
                                    {inputList.length !== 1 && (
                                      <Button
                                        color="primare"
                                        onClick={() => handleRemoveClick(i)}
                                      >
                                        <i className="fa fa-trash" aria-hidden="true"></i>
                                      </Button>
                                    )}
                                    {inputList.length - 1 === i && (
                                      <Button
                                        color="primare"
                                        onClick={handleAddClick}
                                      >
                                        <i className="fa fa-plus"></i>
                                      </Button>
                                    )}
                                  </td>
                                </tr>
                              </tbody>
                            </Table>
                          </div>
                        );  
                      })}
                    </CardBody>
                    <CardFooter >
                    <Row md="12">
                          <Col md="8">
                          </Col>
                          <Col md="4">
                            <FormGroup row>
                              <Label
                                for="exampleEmail"
                                sm={4}
                              >
                                Harga Total
                              </Label>
                              <Col sm={6}>
                              <Input
                                  type="text"
                                  name="barcode"
                                  placeholder="Harga Total"
                                  value={hargatotal}
                                  onChange={(e) => {
                                    setHargaTotal(e.target.value);
                                  }}
                                />
                              </Col>
                            </FormGroup>
                          </Col>
                      </Row>
                    </CardFooter>
                </CardBody>
                <CardFooter>
                      {!isLoading && (
                        <Button color="primary" type="submit">
                          Simpan
                        </Button>
                      )}
                      {isLoading && (
                        <Button color="primary" disabled>
                          <i className="fas fa-spinner fa-spin"></i>
                          {""}
                          loading...
                        </Button>
                      )}
                      <Link className="btn btn-info" to="/admin/po">
                        Kembali
                      </Link>
                </CardFooter>
                </Form>
                <CardBody>
                  <h3>Data PO </h3>
                  <ToolkitProvider
                    rowNumber={rowIndex}
                    data={allPo}
                    keyField="id"
                    columns={[
                      {
                        dataField: "no",
                        text: "#",
                        sort: true,
                        page: 1,
                        formatter: (cell, row, index) => {
                          let currentRow = ++index;
                          return currentRow + rowIndex;
                        },
                      },
                      {
                        dataField: "order_code",
                        text: "Kode PO",
                        sort: true,
                      },
                      {
                        dataField: "supplier",
                        text: "Supplier",
                        sort: true,
                      },
                      {
                        dataField: "keterangan_payment",
                        text: "keterangan Pembayaran",
                        sort: true,
                      },
                      {
                        dataField: "is_cicil",
                        text: "Termin",
                        sort: true,
                      },
                      {
                        dataField: "keterangan_po",
                        text: "Keterangan Po",
                        sort: true,
                      },
                      {
                        dataField: "approve",
                        text: "Status",
                        sort: true,
                        formatter: (cell, row) => {
                          return row.approve === 0
                            ? 'proses'
                            : row.approve === 1
                            ? 'Setuju'
                            : 'Tidak Setuju';
                        },
                      },
                      {
                        dataField: "po_type",
                        text: "Tipe PO",
                        sort: true,
                      },
                      {
                        dataField: "aksi", text: "Aksi", formatter: (cell, row, index) => {
                          return (
                            <ButtonGroup>
                              <Button>
                                <Link
                                  to={redirectPrefix + row.id}
                                  id={"tooltip_" + row.id}
                                >
                                  <i className="fas fa-user-edit" />
                                </Link>
                              </Button>
                              <UncontrolledTooltip delay={0} target={"tooltip_" + row.id}>
                                VALIDASI
                              </UncontrolledTooltip>
                            </ButtonGroup>
                          )
                        }
                      },
                    ]}
                  >
                    {(props) => (
                      <div className="py-4 table-responsive">
                        <BootstrapTable
                          remote
                          {...props.baseProps}
                          bootstrap4={true}
                          bordered={false}
                          hover={true}
                          pagination={paginationFactory({ ...paginationOption })}
                          onTableChange={handleTableChange}
                        />
                      </div>
                    )}
                  </ToolkitProvider>
                </CardBody>
              </Card>
          </div>
        </Row>  
    </>
  );
}

export default PenawaranPo;